$maxWidth: 1960;//ширина макета в граф. редакторе
$minWidth: 320;
$maxWidthContainer: 1904;//ширина ограничивающего контейнера без паддингов
$containerPaddings: 50;//сумма левого и правого паддингов ограничивающего контейнера
$maxContainerWidth: $maxWidthContainer + $containerPaddings;// итоговая ширина ограничивающего контейнера

// colors
$accent: red;
$text: #000;
$text-gray: #8E95A0;
$text-dark: #7F8D9A;

// paths
	// use: background-image: url($icos + 'arrow-right.svg');
$img: '../img/';
$icos: '../img/icons/';
$svg: '../img/icons-svg/';


// media
$mainStop: 1660px;
$lg: 1199px;
$md: 991.98px;
$sm: 767.98px;
$xs: 576px;
$xss: 480px;
$xsss: 375px;
$min: 360px;
$mins: 320px;

// point for multilevel-pnel | размер, на котором multilevel-dropdown превращается в multilevel-panel и наоборот
$multilevelPoint: 1100px;

// Customization Bootstrap vars from "app/libs/bootstrap/scss/_variables.scss"
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px);
$container-max-widths: (sm: 540px, md: 720px, lg: 960px, xl: 1170px);

$gutter: $grid-gutter-width;

$reg: 'MontRegular', sans-serif;
$semi: 'MontSemiBold', sans-serif;
$bold: 'MontBold', sans-serif;
$hev: 'MontHeavy', sans-serif;


